import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    loginUser: "",
    isLogin: false,
    hasFooter: true
  },
  mutations: {
    updateUser(state, user) {
      state.loginUser = user;
    },
    changeFooter(state, footer) {
      state.hasFooter = footer;
    }
  }
})

export default store;


