<template>
  <div class="settings-bar">
    <div class="box-list-arrow-content">
      <ul>
        <li>
          <div class="content">
            <span>头像</span>
            <el-upload
              class="avatar-uploader"
              action="https://jsonplaceholder.typicode.com/posts/"
              :show-file-list="false"
              :on-success="handleAvatarSuccess"
              :before-upload="beforeAvatarUpload"
            >
              <img
                v-if="headPhoto"
                :src="headPhoto"
              >
              <img
                v-else
                src="../../assets/img/user/headImg.png"
              >
            </el-upload>
            <em><i class="sprite s-icon-arrow"></i></em>
          </div>
        </li>
        <li
          v-for="(value,key) in personInfo"
          :key="key"
        >
          <div class="content">
            <span>{{value.title}}</span>
            <label @click="setting(value.type)">{{value.text}}</label>
            <em>
              <i
                v-if="!!value.type"
                class="sprite s-icon-arrow"
              ></i>
            </em>
          </div>
        </li>
      </ul>
    </div>
    <div class="login-out">
      <el-button class="user-btn">退出登录</el-button>
    </div>
  </div>
</template>

<script>
  export default {
    name: "settings",

    data() {
      return {
        headPhoto: "",
        personInfo: [
          { title: "昵称", text: "开发小强", type: "name" },
          { title: "手机号", text: "17723558969" },
          { title: "地区", text: "重庆 渝中区", type: "area" },
          { title: "身份信息", text: "协信员工/业主/其他", type: "person" }
        ]
      };
    },
    methods: {
      setting(tag) {
        switch (tag) {
          case "name":
            this.$prompt("", "请输入昵称", {
              confirmButtonText: "确定",
              cancelButtonText: "取消",
              inputPlaceholder: this.personInfo[0].text,
              inputValidator: function(value) {
                if (value === "" || value === null) {
                  return "昵称不能为空";
                } else if (value.length >= 20) {
                  return "昵称不能超过20个字符";
                }
              }
            })
              .then(({ value }) => {
                this.personInfo[0].text = value;
              })
              .catch(() => {});
            break;
          case "area":
            this.$ajax
              .get("/static/city.json")
              .then(res => {
                console.log(res);
              })
              .catch(err => {
                console.log(err);
              });
            break;
        }
      },
      handleAvatarSuccess(res, file) {
        this.headPhoto = URL.createObjectURL(file.raw);
        this.loading.close();
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === "image/jpeg";
        const isLt2M = file.size / 1024 / 1024 < 2;

        if (!isJPG) {
          this.$message.error("上传头像图片只能是 JPG 格式!");
        }
        if (!isLt2M) {
          this.$message.error("上传头像图片大小不能超过 2MB!");
        }

        this.loading = this.$loading({
          lock: true
        });
        return isJPG && isLt2M;
      }
    },
    created() {},
    mounted() {}
  };
</script>



