<template>
  <div class="item">
    <div class="content">
      <a
        class="link"
        :href="pData.link"
      >
        <label class="name">{{pData.name}}</label>

        <div class="info">
          <label class="rate">
            <span class="number">{{pData.rate}}<em>%</em></span>
            <em class="text">预期年化收益率</em>
          </label>
          <label class="day">
            <span class="number">{{pData.day}}<em>天</em></span>
            <em class="text">投资期限</em>
          </label>
          <label class="money">
            <span class="number">{{investmentAmount}}<em>元</em></span>
            <em class="text">起投金额</em>
          </label>
        </div>

        <div class="explain">
          <label>还款方式：{{pData.repayType}}</label>
          <label
            v-for="(value,key) in pData.explainList"
            :key="key"
          >{{value}}</label>
        </div>

        <div class="progress">
          <label class="line">
            <span :style="{width:ratio}"></span>
          </label>
          <label class="text">
            <span>{{ratio}}</span>
            <span>{{amount}}</span>
          </label>
        </div>

        <slot class="other"></slot>
      </a>
    </div>
  </div>
</template>
<script>
  export default {
    name: "item",
    props: ["productData"],
    data() {
      return {
        pData: this.productData
      };
    },
    computed: {
      ratio: function() {
        if (this.pData.status !== "2") {
          return (
            this.G.count
              .mul(
                this.G.count.div(this.pData.now, this.pData.amount),
                100
              )
              .toFixed(2) + "%"
          );
        } else {
          return "已售罄";
        }
      },
      amount: function() {
        let amount = this.G.count.div(this.pData.amount, 100);
        if (amount >= 10000) {
          amount = this.G.count.div(amount, 10000) + "万元";
        } else {
          amount = amount + "元";
        }
        return amount;
      },
      investmentAmount: function() {
        return this.G.count.div(this.pData.money, 100);
      }
    }
  };
</script>


